import React from 'react';
import style from "./Contacts.module.css"
import styleContainer from "../common/styles/Container.module.css";
import {Title} from "../common/components/title/Title";
import {useFormik} from "formik";
import * as axios from "axios";
import Fade from 'react-reveal/Fade'

const validate = (values) => {
    const errors = {};
    // if (!values.email) {
    //     errors.email = 'Required';
    // } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
    //     errors.email = 'Invalid email address';
    // }
    // if (!values.name) {
    //     errors.name = 'Required';
    // } else if (values.password.length < 5) {
    //     errors.password = 'Must be more then 5 characters';
    // }

    return errors;
};

export function Contacts() {
    const formik = useFormik({
        initialValues: {
            contact:"",
            name: '',
            comment: '',
            // buttonStatus:"active"|"success"
        },
        validate,
        onSubmit: values => {
            axios.post("https://smtp-nodejs-server-my.herokuapp.com/sendMessage", {
                contact:values.contact,
                name:values.name,
                comment:values.comment,
                // buttonStatus:"waiting"|"active"|"success"

            })
                .then((values)=>{
                    // values.buttonStatus = "active"
                    alert('your message has been sent')
                    // values.buttonStatus = "success"
                })
        },
    })

    return (
        <div id={'contact'} className={style.contactsBlock}>
            <div className={`${styleContainer.container} ${style.contactsContainer}`}>
                <div><Title text={"Contacts"}/></div>
                <Fade>
                <form onSubmit={formik.handleSubmit}className={style.form}>
                    {/*<div className={style.form}>*/}
                        <input type="contact"
                               label="contact"
                               placeholder={'Contact'}
                               {...formik.getFieldProps('contact')}
                        />
                        {formik.errors.email ? <div style={{color:'red'}}>{formik.errors.email}</div> : null}
                        <input type="name"
                               placeholder={'Name'}
                               label="name"
                               {...formik.getFieldProps('name')}
                        />
                        <textarea type="comment"
                                  placeholder={'Comment'}
                                  // label="comment"
                                  {...formik.getFieldProps('comment')}
                        />
                    {/*</div>*/}
                    <button type='submit'
                            disabled={formik.isSubmitting}
                            className={style.submitButton}>SEND MESSAGE</button>
                </form>

                </Fade>

            </div>


        </div>
    );
}
